<template>
  Hello {{user}}
  <table>
    <tbody>
      <tr class="w-full lg:w-1/3 m-2" v-for="post in posts">
        <td>
          <h3>
            {{ post.title }}
          </h3>
        </td>

        <td>
          {{ post.createdAt }}
        </td>

        <td>
          {{ post.intro }}
        </td>
      </tr>
    </tbody>
  </table>

</template>

<script>
import axios from 'axios';

export default {
  name: "App",
  data() {
    return {
      user: 'John',
      posts: []
    }
  },
  methods: {
    load() {
      axios.get(`https://62d4757a5112e98e484fa7f0.mockapi.io/posts`)
      .then((response) => {
        this.posts = response.data;
      });
    }
  },
  mounted() {
    this.load();
  }
}
</script>

